.lineup {
    background-color: $rosa;
    padding: $separacion 0;

        h3, p{
            color: $blanco;
            text-align: center;
        }

            .dia {
                text-transform: uppercase;
                margin: $separacion 0;
            }
}

.escenarios-contenedor {
    @include tablet {
        @include grid(2, $separacion)
    }

    .escenario {
        padding-top: $separacion / 2;
        border-radius: .7rem;
    }

    .nombre-escenario {
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &::after { 
            content: ''; //Se coloca con after
            width: 20rem; //Se coloca con after
            height: 10rem; //Se coloca con after
            display: block; //Se coloca con after
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}

.edm .nombre-escenario {
    &::after {
        background-image: url(/build/img/icono_edm.png);

    }
}

.rock .nombre-escenario {
    &::after {
        background-image: url(/build/img/icono_rock.png);
    }
}

.calendario {
    padding: 0; //El padding se representa de color verde
    list-style: none;

    li {
        padding: $separacion / 2;
        font-weight: bold;

        span {
            font-weight: $regular;
        }
    }
}

.bg-amarillo .calendario li:nth-child(odd) {
    background-color: darken($amarillo, 15);
}

.bg-verde .calendario li:nth-child(odd) {
    background-color: darken($verde, 15);
}

