.galeria {
    padding: $separacion 0;

    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0; //Elimina el padding de <ul>
    list-style: none; //Elimina el estilo de la lista
    margin-top: $separacion;

    @include grid(2, $separacion);
        
    @include tablet {
        @include grid(4, $separacion / 2);
    }

    @include desktop {
        @include grid(6, $separacion / 2);
    }
}

.overlay {
    position: fixed; //Hace que la imagen se quede fija y Se debe colocar "top"
    background-color: rgba($color: $negro, $alpha: .8);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center; //Alinea objetos verticalmente y oriontalmente al centro
}

.btn-cerrar {
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $black;
    font-size: 3rem;
    border-radius: 100%;

    &:hover {
        cursor: pointer;
    }
}

.imagen {
    
    @include telefono {
        width: 80%;
    }

    @include tablet {
        width: 70%;
    }

}
