html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    font-family: $fuentePrincipal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden; //No permite dar scroll 
    }
}

p {
    font-size: 2rem;
    color: $negro;
}

.contenedor {
    max-width: 120rem;
    width: 95%; //Estos hace que si la pantalla no puede mostrar el max de 120rem, muestre solo el 95%
    margin: auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 90%;
}

h1, h2, h3 {
    margin: 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}